import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {Redirect} from 'react-router-dom';
import Button from '@material-ui/core/Button';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import slack from '../images/slack_icon.svg';
import userStore from '../stores/UserStore';
import qs from 'query-string';
import axios from 'axios';

const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1
    },
    menuButton: {
        marginRight: theme.spacing(2)
    },
    title: {
        flexGrow: 1
    },
    slack: {
        width: 32,
        height: 32,
        marginRight: theme.spacing(2)
    }
}));

function Main() {
    const classes = useStyles();
    const [authenticated, setAuthenticated] = useState(false);

    useEffect(() => {
        userStore.on('change', () => {
            setAuthenticated(userStore.isAuthenticated());
        });
        const code = qs.parse(window.location.search).code;
        if (code !== undefined) {
            axios.get('https://slack.com/api/oauth.access', {
                params: {
                    client_id: process.env.REACT_APP_OAUTH_CLIENT_ID,
                    client_secret: process.env.REACT_APP_OAUTH_CLIENT_SECRET,
                    code: code,
                    redirect_uri: process.env.REACT_APP_OAUTH_REDIRECT
                }
            }).then((response) => {
                if (response.data['ok']) {
                    const accessToken = response.data['access_token'];
                    const userId = response.data['user_id'];
                    axios.get('https://slack.com/api/users.info', {
                        params: {
                            token: accessToken,
                            user: userId
                        }
                    }).then((response) => {
                        if (response.data['ok']) {
                            userStore.setUser(response.data['user']);
                            userStore.setAccessToken(accessToken);
                            userStore.setAuthenticated(true);
                        }
                        else {
                            // TODO
                        }
                    });
                }
                else {
                    // TODO
                }
            });
        }
    }, []);

    let login = () => {
        const clientId = process.env.REACT_APP_OAUTH_CLIENT_ID;
        const redirectUri = process.env.REACT_APP_OAUTH_REDIRECT;
        window.location = `https://slack.com/oauth/authorize?client_id=${clientId}&scope=users:read&redirect_uri=${redirectUri}`;
    };

    return authenticated ? (<Redirect to='/timesheet'/>) : (
        <div className={classes.root}>
            <AppBar position="static">
                <Toolbar>
                    <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" className={classes.title}>
                        株式会社ベイクラフト
                    </Typography>
                    <Button color="inherit" onClick={login}>
                        <img src={slack} className={classes.slack} alt="slack" />
                        Login with Slack
                    </Button>
                </Toolbar>
            </AppBar>
        </div>
    );
}

export default Main
